<template>
	<div id="editStaffInfor">
		<div class="page_title">
			<router-link :to="{path:'/staffmenu/staff'}">
				<span>员工管理</span>
			</router-link>
			<span class="blue">></span>
			<span class="blue">编辑员工信息</span>
		</div>
		<div class="staffCard">
			<div class="form_title">编辑员工信息</div>
			<div class="from_card">
				<el-form ref="form" :model="form1" label-width="100px" size="mini" :rules="rules">
					<el-form-item label="工号">
						<span>{{form1.employee_id}}</span>
					</el-form-item>
					<el-form-item label="店铺名称">
						<span>{{form.shop_name}}</span>
					</el-form-item>
					<el-form-item label="职位" prop="position_name">
						<el-select v-model="form1.position_name" placeholder="职位" @change="changeIsDisable('form')">
							<el-option v-for="item in allDutyName" :label="item.allDutyName" :value="item" :key="item"></el-option>
						</el-select>
						<el-button class="add-btn" @click="addDuty">新增职位</el-button>
					</el-form-item>
					<el-form-item label="部门" prop="department_name" @change="changeIsDisable('form')">
						<el-select v-model="form1.department_name" placeholder="部门">
							<el-option v-for="item in allDepartName" :label="item" :value="item" :key="item"></el-option>
						</el-select>
						<el-button class="add-btn" @click="addNewDepartment">新增部门</el-button>
					</el-form-item>
					<el-form-item label="手机号">
						<span>{{form2.phone}}</span>
					</el-form-item>
					<el-form-item label="姓名">
						<span>{{form2.real_name}}</span>
					</el-form-item>
					<el-form-item label="身份证">
						<span>{{form2.identity}}</span>
					</el-form-item>
					<el-form-item label="性别">
						<span v-if="form2.sex">{{form2.sex}}</span>
						<span v-else>保密</span>
					</el-form-item>
					<el-form-item label="健康证扫描件">
						<img  class="img" :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+identyImg" v-if="identyImg&&identyImg!==''"/>
						<span v-else>未上传</span>
					</el-form-item>
					<el-form-item label="绑定微信">
						<span v-if="form2.is_weixin ==isBind">绑定</span>
						<span v-else>未绑定</span>
					</el-form-item>
					
				</el-form>
				<div class="btnGroup">
						<el-button class="saveBtn" @click="saveEditStaff('form')" v-if="isDisabled==false">保存</el-button>
						<el-button class="disabledBtn" v-if="isDisabled==true">保存</el-button>
						<el-button class="cancelBtn" @click="cancelEditStaff('form')">取消</el-button>
					</div >
			</div>
		</div>
		<new-department :isShowDialog="isAddNewDepartment" @on-close="hideDialog" :allDepartName="allDepartName"></new-department>
	</div>
</template>

<script>
import { staffDataGet } from "../../api";
import { treeMethods } from "../../index";
import { Public } from "@/config/public";
import newDepartment from "./children/addNewDepartment";
export default {
  data() {
    return {
      imgbase_url: imgbase_url, //图片路径
      dutyForm: [], //获取职位列表得来的关于职位的数据对象，方便像后台传数据发送id
      departForm: [], //获取部门列表得来的关于部门的数据对象，方便像后台传数据发送id
      allDepartName: [], //所有部门的名称，下拉框选择部门
      allDutyName: [], //所有职位名称，下拉框选择职位
      form1: {}, //员工信息表单数据
      form2: {}, //个人信息表单数据
      isBind: 0,
      identyImg: "",
      isDisabled: false,
      shopId: "",
      addedDuty: "",
      form: {}, //获取的整个数据
      isAddNewDepartment: false, //是否显示添加新部门的模态框
      rules: {
        position_name: [
          {
            required: true,
            message: "请选择职位",
            trigger: "change"
          }
        ],
        department_name: [
          {
            required: true,
            message: "请选择部门",
            trigger: "change"
          }
        ]
      }
    };
  },
  components: {
    newDepartment
  },
  created() {
    this.shopId = Public.getShopid();
    this.initData();
    this.getAllDepartName();
    this.getAllDutyName();
  },
  methods: {
    //初始化表单数据
    initData() {
      this.employee_id = this.$route.query.employeeId;
      let data = {
        get_employee_info: 1,
        shop_id: this.shopId,
        employee_id: this.employee_id
      };
      staffDataGet.getStaffList(data, resp => {
        this.form = resp.data;
        this.identyImg = resp.data.userinfo.health_certificate;
        this.form1 = resp.data.employee_info;
        this.form2 = resp.data.userinfo;
      });
    },

    //点击保存
    saveEditStaff(formName) {
      let position_id = treeMethods.getDutyDepartId(
        this.dutyForm,
        this.form1.position_name
      );
      let department_id = treeMethods.getDutyDepartId(
        this.departForm,
        this.form1.department_name
      );
      this.$refs[formName].validate(valid => {
        if (valid) {
          let data = {
            save_employee_info: 1,
            employee_id: this.form1.employee_id,
            department_id: department_id,
            position_id: position_id
          };
          staffDataGet.getUserInfor(data, this.goBack);
        }
      });
    },

    //点击添加新部门
    addNewDepartment() {
      this.isAddNewDepartment = true;
    },

    //点击添加新职位
    addDuty() {
      sessionStorage.setItem(
        "lastDuty",
        this.allDutyName[this.allDutyName.length - 1]
      );
      this.$router.push({
        path: "/staff/editDuty",
        query: {
          all: this.allDutyName
        }
      });
    },
    //获取部门列表
    getAllDepartName() {
      let data = {
        get_department_list: 1
      };
      staffDataGet.getDepartList(data, resp => {
        this.departForm = resp.data.department_list;
        resp.data.department_list.forEach(item => {
          if (this.allDepartName.indexOf(item.department_name) == -1) {
            this.allDepartName.push(item.department_name);
          }
        });
      });
    },

    //获取职位列表
    getAllDutyName() {
      let data = {
        get_position_list: 1
      };
      staffDataGet.getDutyList(data, resp => {
        this.dutyForm = resp.data.position_list;
        resp.data.position_list.forEach(item => {
          if (this.allDutyName.indexOf(item.position_name) == -1) {
            this.allDutyName.push(item.position_name);
          }
        });
        this.addedDuty = this.allDutyName[this.allDutyName.length - 1];
        let isNew = sessionStorage.getItem("lastDuty");
        if (isNew !== null && isNew != this.addedDuty) {
          this.form1.position_name = this.addedDuty;
        }
      });
    },
    //切换可点击状态
    changeIsDisable(formName) {
      this.isCanClick(formName);
    },
    //判断是否可点击
    isCanClick(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.isDisabled = false;
        }
      });
    },

    //点击取消
    cancelEditStaff(formName) {
      this.$refs[formName].resetFields();
      this.goBack();
    },

    //隐藏弹框
    hideDialog(data) {
      this.form1.department_name =
        data !== "" && data !== undefined ? data : this.form1.department_name;
      this.allDepartName.push(data);
      this.getAllDepartName();
      this.isAddNewDepartment = false;
    },

    //返回员工管理页面
    goBack() {
      this.$router.push("/staffmenu/staff");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.staffCard {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.from_card {
  width: 500px;
  padding: 48px 0 40px 0;
  margin-left: 133px;
}

.page_title {
  @include wh(100%, 16px);
  @include fc(12px, #333333);
  margin-bottom: 14px;
}

.form_title {
  @include wh(100%, 40px);
  @include fc(14px, #666666);
  line-height: 40px;
  background-color: #f6f8fc;
  padding-left: 14px;
}

.img {
  width: 200px;
  height: 120px;
}

.pinpo {
  font-size: 12px;
  color: #9b9b9b;
}

.add-btn {
  margin-left: 14px;
  cursor: pointer;
}

.blue {
  font-size: 12px;
  color: #4877e7;
  cursor: pointer;
}
.btnGroup {
  width: 200%;
}
</style>
<style lang="scss">
@import "src/styles/mixin.scss";
#editStaffInfor {
  .el-button {
    @include wh(80px, 30px);
    border: 1px solid #4877e7;
    color: #4877e7;
    border-radius: 5px;
    font-size: 12px;
    cursor: pointer;
  }
  .el-button.disabledBtn {
    @include wh(160px, 40px);
    @include fc(14px, #ffffff);
    border: 1px solid #d8d8d8;
    cursor: pointer;
    color: #ffffff;
    margin: 0 20px 0 30%;
    background: #d8d8d8;
  }
  .el-button.saveBtn {
    @include wh(160px, 40px);
    @include fc(14px, #ffffff);
    margin: 0 20px 0 30%;
    border: none;
    background: #4877e7;
  }
  .cancelBtn.el-button {
    @include wh(160px,40px);
    @include fc(14px, #4877e7);
    margin: 0 20px 0 0;
  }
  .el-form-item__label {
    opacity: 0.57;
    font-size: 14px;
    color: #000000;
  }
  .el-form-item__content {
    font-size: 14px;
    color: #333333;
  }
}
</style>


